.odc-icon-dropdown img.icon {
  background-color: var(--pf-v5-global--palette--white);
  border-radius: var(--pf-v5-global--BorderRadius--sm);
  height: var(--pf-v5-global--FontSize--3xl);
  margin-right: var(--pf-v5-global--spacer--sm);
  padding: var(--pf-v5-global--spacer--xs);
  width: var(--pf-v5-global--FontSize--3xl);
}

.odc-icon-dropdown__item {
  display: flex;
  align-items: center;
}

.odc-icon-dropdown .pf-v5-c-dropdown__toggle-text {
  display: flex;
  align-items: center;
}

.odc-icon-dropdown__menu {
  max-height: 300px;
}

.odc-icon-dropdown__menu li>a {
  display: flex;
  align-items: center;
}

